
import {Options, Vue} from 'vue-class-component';
import {CostOfOwnership} from "../model/cost_of_ownership"
import {LoanAmount} from "../model/loan_amount"
import {Expense} from "../model/expense"
import {format as money_format} from 'v-money3';
import {Params} from "../model/param"

@Options({
    props: {
      msg: String,
      loan_amount: LoanAmount,
      cost_of_ownership: CostOfOwnership,
      params: Params,
    }
})

export default class OutputTable extends Vue {
    child_visible = true;
    view_period = Expense.ONE_WEEK;
    alias_money_format = money_format;
    describe_period=Expense.describe_period;
    format(v: number) : string {
      return "$" + money_format(v);
   }

}

