
import {Options, Vue } from 'vue-class-component';
import {Params} from "../model/param"
import {PostcodeToState} from "../model/aus_state"
import {Postcode2Lga} from "../model/postcode2lga"
import {TaxBracket} from  "../model/marginal_tax"

@Options({
  props: {
    msg: String,
    params: Params
  },
})
export default class InputParam extends Vue {
  msg!: string;
  params!: Params;
  deposit_percent=20;
  lga(postcode: number) : string {
      return Postcode2Lga(postcode);
  }
  state(postcode: number) : string {
      return PostcodeToState(postcode);
  }
  tax_rate(enable: boolean, income: number) : string {
      if (enable) {
          let pct = TaxBracket.GetPercent(income)*100;
          return `${pct}%`;
      } else {
          return "N/A";
      }
  }

}
